<template>
    <div class="treebar">
        <!--  公共组件  -->
        <ul class="navigation">
            <div><a href="javascript:void(0);"><img :src="avatar" alt=""><p></p></a></div>
            <router-link :to="{path:'/treeupgrade', query:{treeId:tree_id}}" tag="li" class="sj"><i></i><p>升级</p></router-link>
            <router-link :to="{path:'/treeteam', query:{treeId:tree_id}}" tag="li" class="td"><i></i><p>团队</p></router-link>
            <router-link :to="{path:'/treeinvite', query:{treeId:tree_id}}" tag="li" class="yq"><i></i><p>邀请</p></router-link>
            <router-link :to="{path:'/treefinance', query:{treeId:tree_id}}" tag="li" class="cw"><i></i><p>财务</p></router-link>
            <router-link :to="{path:'/treeinfo', query:{treeId:tree_id}}" tag="li" class="xx active"><i></i><p>信息</p></router-link>
        </ul>
        <div class="homeBtn" id="HomeBtn">
            <div @click="gohome">
                <p>
                    <span>主页</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    export default {
        name: "treeBar",
        props:['tree_id'],
        data()
        {
            return {
                activeNav:''
            }
        },
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        mounted()
        {
            var homeBtn = document.getElementById("HomeBtn");
            var width = document.documentElement.clientWidth;
            var height = document.documentElement.clientHeight;
            homeBtn.addEventListener('touchmove', function (event) {
                event.preventDefault();
                if (event.targetTouches.length == 1) {
                    let touch = event.targetTouches[0];
                    let moveleft = touch.pageX-22;
                    let movetop = touch.pageY-25;
                    if(moveleft<0){moveleft=0;}
                    if(moveleft>(width-43)){ moveleft = width - 43}
                    if (movetop < 0) {movetop = 0;}
                    if (movetop > (height-50)) { movetop = height - 50; }
                    homeBtn.style.top = movetop+'px';
                    homeBtn.style.left = moveleft + 'px';
                };
            });

            this.activeNav = this.$route.path;
        },
        methods:{

            gohome()
            {
                this.$router.push('/')
            }
        }
    }
</script>

<style scoped>
   @import "../../../static/css/treebar.css";

   .treebar .homeBtn {
       box-shadow: 0 3px 16px 2px rgba(0, 0, 0, 0.1);
       display: inline-block;
       background-color: rgba(0, 0, 0, 0.16);
       width: 0.6rem;
       height: 0.6rem;
       border-radius: 50%;
       font-size: 0.14rem;
       position: fixed;
       bottom: 0.3rem;
       right: 0.1rem;
       z-index: 9999;
       text-align: center;
   }
   .treebar .homeBtn div {
       height: 0.4rem;
       width: 0.4rem;
       background-color: rgba(0, 0, 0, 0.3);
       border-radius: 50%;
       display: inline-block;
       text-align: center;
       margin-top: 0.1rem;
   }
   .treebar .homeBtn div p {
       height: 0.3rem;
       width: 0.3rem;
       background-color: rgba(0, 0, 0, 0.46);
       border-radius: 50%;
       display: inline-block;
       text-align: center;
       margin-top: 0.05rem;
   }
   .treebar .homeBtn div p span {
       font-size: 0.11rem;
       color: #fff;
       line-height: 0.3rem;
       text-align: center;
   }
</style>



