import request from '@/utils/request'

// 获取所有币种
export function redList(params) {
    return request({
        url: '/redgroup/list',
        method: 'get',
        params
    })
}


// 创建
export function createRed(data) {
    return request({
        url: '/redgroup/create',
        method: 'post',
        data
    })
}

