import request from '@/utils/request'

// 获取所有币种
export function treeList(params) {
    return request({
        url: '/tree/treeList',
        method: 'get',
        params
    })
}
// 获取财富树头部信息
export function treeHead(params) {
    return request({
        url: '/tree/treeHead',
        method: 'get',
        params
    })
}
// 财富树流水
export function treeLog(params) {
    return request({
        url: '/tree/treeLog',
        method: 'get',
        params
    })
}

// 财富树信息
export function jointreeInfo(params) {
    return request({
        url: '/tree/treeinfo',
        method: 'get',
        params
    })
}
// 加入财富树
export function joinTree(data) {
    return request({
        url: '/tree/joinTree',
        method: 'post',
        data
    })
}

export function treeInfo(params) {
    return request({
        url: '/tree/tree',
        method: 'get',
        params
    })
}

// 财富树邀请信息
export function treeInvite(params) {
    return request({
        url: '/tree/treeInvite',
        method: 'get',
        params
    })
}

// 财富树邀请信息
export function treeTeam(params) {
    return request({
        url: '/tree/treeUser',
        method: 'get',
        params
    })
}


// 升级信息
export function upgradeInfo(params) {
    return request({
        url: '/tree/upgradeInfo',
        method: 'get',
        params
    })
}

// 升级
export function upgrade(data) {
    return request({
        url: '/tree/upgrade',
        method: 'post',
        data
    })
}

// 创建
export function createTree(data) {
    return request({
        url: '/tree/createTree',
        method: 'post',
        data
    })
}

// 财富树攻略
export function treestrategy(params) {
    return request({
        url: '/tree/treestrategy',
        method: 'get',
        params
    })
}







